import React from 'react';
import PropTypes from 'prop-types';
import Headroom from 'react-headroom';
import Helmet from 'react-helmet';

import Theme from '../styles/theme';
import AppBar from '../components/app-bar';
import Footer from '../components/footer';
import PoweredByStrava from '../assets/api_logo_pwrdBy_strava_horiz_gray.svg';
// import ConnectWithStrava from '../assets/btn_strava_connectwith_light.svg';
import '../css/prism-red-light.css';


class Template extends React.Component {
  static propTypes = {
    children: PropTypes.func,
  }

  render() {
    return (
      <Theme>
        <Helmet
          title="A Race athlete"
          meta={[
            { name: 'description', content: 'Using your Strava cycling metrics to help you reach your goals' },
            { name: 'keywords', content: 'cycling, endurance, training, goals, motivation' },
          ]}
        />
        <div style={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }} >
        <Headroom>
          <AppBar
            // rightImgSrc={ConnectWithStrava}
            // rightOnClick={() => window.location.assign('/auth/strava')}
            // rightText="Connect with Strava"
            leftOnClick={() => window.location.assign('../')}
            title="A Race athlete Blog"
          />
        </Headroom>

          <div >
            {this.props.children()}
          </div>
          <div style={{ marginTop: 'auto' }}>
            <Footer
              brand="rcrsv.com"
              info="This app came about because I wanted to use it. It is a labor of love and suffering, much like cycling."
              imgAlt="Powered By Strava"
              imgLink="https://www.strava.com"
              imgSrc={PoweredByStrava}
            />
          </div>
        </div>
      </Theme>
    );
  }
}

export default Template;



